export default
function windowScrollPosition(propertyName) {
  return {
    data() {
      return {
        // 스크롤 위치 초기화
        [propertyName]: 0
      }
    },
    created() {
      if (!this.$isServer) {
        // isServer, 현 네트워크가 서버인가를 True/False로 알려주는 프로퍼티
        // 즉, 이 코드를 클라이언트 서버 상에서만 작동하도록!
        this._scrollListener = () => {
          this[propertyName] = Math.round(window.pageYOffset)
          // Math.round(), 입력값을 반올림한 수와 가장 가까운 정수 값을 반환
        }

        // 스크롤 위치 업데이트
        this._scrollListener()

        // 스크롤 이벤트를 window에 등록하고 스크롤 위치 업데이트
        window.addEventListener('scroll', this._scrollListener)
      }
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this._scrollListener)
    }
  }
}
